function Filter1({ filterState, setFilterState, payload, setPayload }) {
	return (
		<div className="quicksearch-fade-in">
			<button
				type="button"
				className="btn btn-primary m-3"
				onClick={() => {
					(() => {
						document
							.getElementsByClassName('quicksearch-fade-in')[0]
							.classList.add('quicksearch-fade-out');
					})();
					setTimeout(() => {
						setFilterState({ ...filterState, filter1: false, filter2: true });
						setPayload({ ...payload, filter1: 'filter1 first' });
					}, 5000);
				}}
			>
				filter1 first
			</button>

			<button
				type="button"
				className="btn btn-primary m-3"
				onClick={() => {
					(() => {
						document
							.getElementsByClassName('quicksearch-fade-in')[0]
							.classList.add('quicksearch-fade-out');
					})();
					setTimeout(() => {
						setFilterState({ ...filterState, filter1: false, filter2: true });
						setPayload({ ...payload, filter1: 'filter1 first' });
					}, 5000);
				}}
			>
				filter1 second
			</button>
		</div>
	);
}

export default Filter1;
